import clotifyThumbnail from "./../../../assets/images/projects/clotify-thumbnail.webp";
import clotify1 from "./../../../assets/images/projects/clotify1.webp";
import cloon1 from "./../../../assets/images/projects/cloon1.webp";
import cloon2 from "./../../../assets/images/projects/cloon2.webp";
import cloon3 from "./../../../assets/images/projects/cloon3.webp";
import cloonThumbnail from "./../../../assets/images/projects/cloon-thumbnail.webp";
import poker1 from "./../../../assets/images/projects/poker1.webp";
import pokerThumbnail from "./../../../assets/images/projects/poker-thumbnail.webp";
import weather1 from "./../../../assets/images/projects/weather1.webp";
import weather2 from "./../../../assets/images/projects/weather2.webp";
import weather3 from "./../../../assets/images/projects/weather3.webp";
import weather4 from "./../../../assets/images/projects/weather4.webp";
import weatherThumbnail from "./../../../assets/images/projects/weather-thumbnail.webp";
import mangaplanet1 from "./../../../assets/images/projects/mangaplanet1.webp";
import mangaplanet2 from "./../../../assets/images/projects/mangaplanet2.webp";
import mangaplanet3 from "./../../../assets/images/projects/mangaplanet3.webp";
import mangaplanet4 from "./../../../assets/images/projects/mangaplanet4.webp";
import mangaplanetThumbnail from "./../../../assets/images/projects/mangaplanet-thumbnail.webp";
import pacman1 from "./../../../assets/images/projects/pacman1.webp";
import pacmanThumbnail from "./../../../assets/images/projects/pacman-thumbnail.webp";

import ProjectCard from "../../UI/ProjectCard/ProjectCard";

const Portfolio = () => {
  return (
    <section className={styles.portfolio} id="projekty">
      <Fade bottom duration={650}>
        <h2 className="section-heading">
          Moje <span>wyrnione</span> projekty
        </h2>
      </Fade>
      <div className={styles.porfolio_slider}>
        <Splide
          options={{
            perPage: 3,
            perMove: 1,
            pagination: false,
            rewind: true,
            breakpoints: {
              950: {
                perPage: 2,
              },
              650: {
                perPage: 1,
                padding: "16px",
                arrows: false,
              },
            },
          }}
        >
          <SplideSlide>
            <ProjectCard
              name="Clotify"
              images={[clotifyThumbnail, clotify1]}
              description="Klon spotify'a. Uytkownik posiada moliwo odsuchiwania muzyki, dodawania najlepszych albumw i piosenek do ulubionych, przegldania profili innych uytkownik. 
              \n \n
              Podczas odsuchiwania piosenek uytkownik ma moliwo przyciszania, zatrzymywania utworu i korzystania z historii.
              "
              technologies={["React", "Redux", "React Router"]}
              githubUrl="https://github.com/Kulizon/Clotify"
            ></ProjectCard>
          </SplideSlide>
          <SplideSlide>
            <ProjectCard
              name="MangaPlanet"
              images={[
                mangaplanetThumbnail,
                mangaplanet1,
                mangaplanet2,
                mangaplanet3,
                mangaplanet4,
              ]}
              description="Strona przeznaczona dla fanw mang. Uytkownik posiada do dyspozycji wiele narzdzi uatwiajcych czytanie, midzy innymi:  wyszukiwanie mang po nazwie, dodawanie do ulubionych, do listy na pniej, do przeczytanych, pisanie i czytanie recenzji, a take komentarzy do poszczeglnych rozdziaw.
              \n \n
              Na stronie gwnej generowane s propozycje mang wedug rnych kategori. Uytkownik posiada rwnie woln rk w dostosowywaniu swojej nazwy, zdjcie profilowego i zdjcia w tle. Posiada moliwo przegldania kont innych czytelnikw i ledzi ich dziaania.
              \n \n
              Na stronie czytelniczej odwiedzajcy ma moliwo powikszenia i pomniejszenia paneli, zmiany ich jakoci, a take przeczenia pomidzy rnym uoeniem skanw stron. "
              technologies={[
                "React",
                "Redux",
                "Typescript",
                "Strapi",
                "React Router",
              ]}
              githubUrl="https://github.com/Kulizon/MangaPlanet-Front"
            ></ProjectCard>
          </SplideSlide>
          <SplideSlide>
            <ProjectCard
              name="Cloon"
              images={[cloonThumbnail, cloon1, cloon2, cloon3]}
              description="Aplikacja podobna do Omegle, jednak odwiedzajcy moe nie tylko doczy do losowego pokoju, ale rwnie stworzy swj wasny lub doczy do konkretnego, wic rwnie dobrze moe ona suy do komunikacji podobnej jak w aplikacji Zoom, czy Google Meet.
              \n \n
              Uytkownik ma moliwo wyciszenia i wyczenia kamery zarwno swojej, jak i uytkownika, z ktrym jest poczony. Ponadto kady pokj posiada swj chat oraz koo fortuny, w ktrym uytkownicy mog dodawa i usuwa wasne opcje."
              technologies={[
                "React",
                "Redux",
                "Typescript",
                "Node.js",
                "Socket.IO",
                "Express",
                "WebRTC",
                "React Router",
              ]}
              githubUrl="https://github.com/Kulizon/Cloon"
            ></ProjectCard>
          </SplideSlide>
          <SplideSlide>
            <ProjectCard
              name="Weather.IO"
              images={[
                weatherThumbnail,
                weather1,
                weather2,
                weather3,
                weather4,
              ]}
              description="Prosta i szybka aplikacja suc do sprawdzania pogody w dowolnej, wpisanej przez uytkownika lokalizacji. Powstaa przy uyciu popularnego OpenWeather API i bya jedn z pierwszych z stworzonych przeze mnie aplikacji w frameworku React. 
              \n \n Po wpisaniu wybranego adresu generowana jest aktualna pogoda oraz prognoza na nastpny tydzie. Odwiedzajcy posiada moliwo zmiany lokalizacji i preferowanej jednostki temperatury w dowolnym momencie."
              technologies={["React", "Redux", "React Router"]}
              githubUrl="https://github.com/Kulizon/WeatherApp"
            ></ProjectCard>
          </SplideSlide>
          <SplideSlide>
            <ProjectCard
              name="TexasHoldem"
              images={[pokerThumbnail, poker1]}
              description="Jest to gra pokerowa w modelu Texas Holdem. Uytkownik posiada moliwo gry z komputerem, ktre podejmuje decyzj zalenie od posiadanych kart oraz tych ju wyoonych na stole, a take kwoty, do jakiej musi podbi.
              \n \n
              Gracz moe zmiania domylne wartoci, takie jak: ilo przeciwnikw, balans na start, a take prdko, z jak gra komputer."
              technologies={["React", "Redux", "Typescript"]}
              githubUrl="https://github.com/Kulizon/TexasHoldem"
            ></ProjectCard>
          </SplideSlide>
          <SplideSlide>
            <ProjectCard
              name="Pacman"
              images={[pacmanThumbnail, pacman1]}
              description="Pacman napisany w jzyku C w ramach projektu zaliczeniowego na studiach. Projekt wykorzystuje bibliotek graficzn SDL."
              technologies={["C", "SDL"]}
              githubUrl="https://github.com/Kulizon/PacMan"
            ></ProjectCard>
          </SplideSlide>
          {/* <SplideSlide>
            <ProjectCard
              name="Avers"
              images={[aversThumbnail, avers1, avers2, avers3, avers4, avers5, avers6]}
              description="Komercyjna strona dla firmy projektowej. Posiada dwie proste zakadki, w ktrych znajduje si midzy innymi galeria zdj oraz mapa, ktra funkcjonuje przy pomocy API Google Maps. Projekt graficzny wykonany cakowicie przeze mnie."
              technologies={["React", "Typescript", "React Router"]}
              githubUrl=""
            ></ProjectCard>
          </SplideSlide> */}
        </Splide>
      </div>
    </section>
  );
};

export default Portfolio;
